.Login {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.Welcome {
  margin-bottom: 4rem;
}
.Title {
  font-weight: 500;
  font-size: 2.8rem;
  /* margin:1rem; */
}
.Login form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: min(40rem, 80%);
}
.Input {
  border: 0;
  border-bottom: 2px solid gray;
  margin: 1rem;
  width: 100%;
  font-size: 1.8rem;
  padding: 0.5rem 1rem;
}
.Input:focus {
  outline: none;
}
.Login button {
  border: 0;
  background-color: #61acb3;
  border: 50%;
  color: white;
  font-size: 2rem;
  width: 80%;
  padding: 0.8rem;
  border-radius: 2rem;
  margin: 3rem 0;
}
.ForgotSection {
  display: flex;
  justify-content: space-between;
  width: 105%;
  margin: 3rem 0;
}
.Remember {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.Remember input {
  margin-right: 1rem;
}

.Forgot {
  color: #61acb3;
}
.Terms {
  font-size: 1.3rem;
  position: absolute;
  bottom: 1rem;
  left: 50%;
  transform: translate(-50%);
  width: 100%;
  text-align: center;
}
.Switch{
  color:#61acb3;
  font-size: 1.4rem;
  margin-bottom: 3rem;

}
